.greetingForm {
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
    height: 36px;
}

.error {
    margin-top: 7px;
    color: #CC0000;
    font-size: 14px;
}

.input {
    min-width: 362px;
    min-height: 36px;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    padding: 8px 0 8px 12px;

    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 500;
}

.input:focus {

}

.errorInput {
    min-width: 362px;
    min-height: 36px;
    border: 1px solid #CC0000;
    border-radius: 5px;
}

.button {
    min-width: 78px;
    max-height: 36px;
    border-radius: 3px;
    padding: 5px 24px;
    background-color: #0066CC;

    font-weight: 600;
    font-size: 14px;
    color: #FFFFFF;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    opacity: 50%;
}

.text {
    margin-bottom: 9px;
    margin-top: 31px;
    font-size: 14px;
    color: #00000080;
}

.greeting {
    margin-top: 10px;

    font-size: 14px;
    font-weight: 500;
}
