.sidebar {
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    position: absolute;
    min-width: 317px;
    padding: 16px 24px 16px 70px;
    height: 100%;
    z-index: 2;

    background-color: #FFFFFF;
}

.background {
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.3;
    position: absolute;
    z-index: 1;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
}

.nav a {
    text-decoration: none;
    font-size: 22px;
    font-weight: 600;
    color: #000000;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a:active {
    color: #3D6B99;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    align-self: flex-end;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
    width: 24px;
    height: 24px;

    cursor: pointer;

    background: none;
    border: none;
}

.close img {
    width: 100%;
}
