.button {
   min-width: 130px;
    height: 30px;
    padding: 5px 24px;
    border-radius: 3px;
    border: none;

    color: #FFFFFF;
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 600;
}

.disabled {
    color: #002E5C;
    background-color: #004D99;
    opacity: 0.5;
}

.secondary {
    color: #0066CC;
    background-color: transparent;
    border: 1px solid #0066CC;
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
}
